.header{
  background-color: #FFB8CA;
  padding-top: 37px;
   }
  .header-container{
  display: flex;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  max-width: 320px;
  @media screen and (min-width: 768px) {
      padding-left: 10px;
      padding-right: 10px;
      max-width: 700px;
  }
  @media screen and (min-width: 1280px) {
      padding-left: 15px;
      padding-right: 15px;
      max-width: 1150px;
  }
  }
  .header-navigation{
  
   @media screen and (min-width: 1280px){
     display: flex;
    } 
  }
.header-logo{
  display: block;
width: 199px;
height: 50px;
margin-right: 61px;
@media screen and (min-width: 1280px){
margin-right: auto;
}
@media screen and (min-width: 1280px){
  margin-right: 110px;
}
}
.header-list{
  display: none;
  @media screen and (min-width: 1280px){
     display: flex;
     align-items: center;
    } 
  
}
.header-link{
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 1.31;
color: #FFFFFF;
&:hover,
&:focus{
  color: #D41443;
}
}
.header-item:not(:last-child){
margin-right: 35px;
}

.header-btn{
  
  @media screen and (max-width:767.5px){
    display: none;
  }
  display: flex;
width: 159px;
height: 44px;
border-radius: 22px;
border:none;
background: #ffffff;
color: #D41443;
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 1.31;
align-items: center;
justify-content: center;
}
.header-button{
margin-left: auto;
display: flex;
}



.open-menu-btn{
  margin-right: 30px;
  // position: absolute;
    display: flex;
  justify-content: end;
  align-items: center;
  // right: 235px;
  // top: 53px;
  background-color: transparent;
  border: none;
    @media screen and (min-width: 1280px){
    display: none;
  }
}
.open-menu__svg{
fill: #D41443;
}

